body {
  font-family: 'Roboto Light', sans-serif;
  font-weight: 100;
  margin: 0;
  padding: 0;
  letter-spacing: 0.5px;
  line-height: 1.5;
  transition: background-color 0.3s;






}

/* Light mode styles */
body.light-mode {
  background-color: var(--background-color);
  color: var(--text-color);

  --background-color: #ffffff; /* Light mode background color */
  --text-color: #333333; /* Light mode text color */
  --primary-color: #007BFF;
  --secondary-color: #6C757D;
  --accent-color: #FF6B6B;
  --header-color: #f8f9fa;
  --footer-color: #f8f9fa;
}

/* Dark mode styles */
body.dark-mode {
  background-color: var(--background-color);
  color: var(--text-color);

  
  --background-color: #121212; /* Dark mode background color */
  --text-color: #ffffff; /* Dark mode text color */
  --primary-color: #BB86FC;
  --secondary-color: ##03DAC5;
  --accent-color: #FF4081;
  --header-color: #121212;
  --footer-color: #121212;
}

#root {
  text-align: center;
  padding: 2rem;


  

}